import React from 'react';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import cssVar from 'views/mobileView/styles';
import Modal from 'react-bootstrap/Modal';
import FundConstituents from 'views/mobileView/components/molecules/Cards/FundConstituents';
import PlanDetailsModal from './Modals/PlanDetailsModal';
import { AiOutlineExclamationCircle } from 'react-icons/ai';

export default function RecommendedMintbox(props) {

    const [showPortfolioModal, setShowPortfolioModal] = React.useState(false)
    const [showDetailsModal, setShowDetailsModal] = React.useState(false)
    const STATE = props.location.state;
    
    const PortfolioModal = () => {
        return (
            <Modal
                className="bottom-drawer"
                show={showPortfolioModal}
                onHide={() => setShowPortfolioModal(false)}
                keyboard={false}
            >
                <Modal.Body style={{ height: "80vh", overflow: "scroll" }} className='p-0'>
                    <FundConstituents fundConstituents={STATE.fundData.Constituents} type='only-constituents' />
                </Modal.Body>
                <Modal.Footer>
                    <button className="solidButton" onClick={() => setShowPortfolioModal(false)}>
                        Close
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }

    if (STATE === undefined) {
        props.history.push('/Goals')
        return <></>
    }
    return (
        <div className='mainContainer'>
            <MainHeader
                header={STATE.header}
                right="Plan details"
                rightAction={()=>setShowDetailsModal(true)}
                navigation={props.history}
            />
            <div className="dataContainer">
                <div style={{display:'flex' , alignItems:'center',marginTop: "6%",marginBottom: "4%"}}>
                    <div className='header1dark' style={{ flex:1,color: cssVar.getPropertyValue('--darkGreen') }}>
                        Recommended allocation
                    </div>
                    <div className='greenChipHeader' onClick={()=>setShowPortfolioModal(true)}>See constituents</div>
                </div>
                <div style={{ border: "1px solid "+cssVar.getPropertyValue('--light'),borderRadius:'8px'}}>
                    <FundConstituents fundConstituents={STATE?.fundData?.Constituents} type='goal-recom'/>
                </div>
                {
                    STATE?.fundData?.Constituents?.some(c=>c.AssetClassId === 3) ?
                    <div style={{ marginTop: '4%', padding: '4%', backgroundColor: cssVar.getPropertyValue('--lightest'), borderRadius: '8px' }}>
                        <div style={{ display: 'flex', gap: '3%', alignItems: 'center',marginBottom:'2%' }}>
                        <AiOutlineExclamationCircle size={16} style={{ color: cssVar.getPropertyValue('--internationalEquity') }} />
                        <div className='header2dark'>Important note</div>
                        </div>
                        <div className='smallTextLight'>
                        This goal's constituents include US ETFs that are trading at premium due to SEBI’s restrictions. Once the restrictions are lifted, we will add US ETFs through rebalancing.
                        </div>
                    </div>
                    :
                    <></>
                }
            </div>
            <div className='footer'>
                {/* <button className="outlineButton">Save for later</button> */}
                <button
                    className='solidButton'
                    onClick={() => props.history.push({
                        pathname: '/Goals/achieve-goal',
                        state: { ...props.location.state }
                    })
                    }
                >
                    Invest for free
                </button>
            </div>
            <PortfolioModal />
            <PlanDetailsModal STATE={STATE} showDetailsModal={showDetailsModal} onClose={()=>setShowDetailsModal(false)} lumpsum={STATE.oneTime}/>
        </div>
    )
}
