import Modal from "react-bootstrap/Modal"
import { CurrencyFormat } from "views/mobileView/components/atoms/CurrencyFormat"

export default function PlanDetailsModal({STATE, showDetailsModal, onClose, lumpsum}) {

    const yourResponse = STATE?.type === 'retirement'? 
    [
        {key: "Monthly expenses", value: CurrencyFormat(STATE?.monthlyExpenses)},
        {key: "Present age", value: STATE?.presentAge + ' years'},
        {key: "Retirement age", value: STATE?.retirementAge + ' years'},
        {key: 'Upfront investment', value: CurrencyFormat(lumpsum || STATE?.onetime)},
        {key: 'Annual increase in SIP', value: STATE?.monthlyIncrease + '%'},
        {key: "PF balance", value: CurrencyFormat(STATE?.pfBalance)},
        {key: "Monthly PF contribution", value: CurrencyFormat(STATE?.pfMonthlyContribution)},
    ]
    :
    [
        {key: STATE?.planDetails?.presentCost, value: CurrencyFormat(STATE?.presentCost)},
        {key: STATE?.planDetails?.goalDue, value: STATE?.goalDue + ' years'},
        {key: 'Upfront investment', value: CurrencyFormat(lumpsum || STATE?.onetime)},
        {key: 'Annual increase in SIP', value: STATE?.monthlyIncrease + '%'},
    ]
    const ourAssumption = STATE?.type === 'retirement'? 
    [
        {key: 'Inflation', value: '6%'},
        {key: 'Capital gains tax', value: '10%'},
        {key: 'PF intrest rate', value: '8%'},
        {key: 'Life expenctancy', value: '90 years'},
    ]
    :
    [
        {key: STATE?.planDetails?.inflation, value: (STATE.type === 'education' ? 10 : STATE.type === 'house' ? 8 : STATE.type === 'custom' ? STATE.inflation:6) + '%'},
        {key: 'Capital gains tax', value: '10%'},
    ]

    return (
        <Modal
            className="bottom-drawer"
            show={showDetailsModal}
            onHide={() => onClose()}
            keyboard={false}
        >
            <Modal.Body>
                <div className='header1dark'>Your responses</div>
                <ul>
                    {
                        yourResponse.map((a,i) => {
                            return <li key={i} style={{ margin: "4% 0" }} className='bodyTextLight'>{a.key + ':'}
                                <span className='bodyTextDark'> {a.value}</span></li>
                        })
                    }
                </ul>
                <div className="header1dark">Our assumptions</div>
                <ul>
                    {
                        ourAssumption.map((a,i) => {
                            return <li key={i} style={{ margin: "4% 0" }} className='bodyTextLight'>{a.key + ':'}
                            <span className='bodyTextDark'> {a.value}</span></li>
                        })
                    }
                </ul>
            </Modal.Body>
            <Modal.Footer>
                <button className="solidButton" onClick={() => onClose()}>
                    Close
                </button>
            </Modal.Footer>
        </Modal>
    )
}
